import angular from 'angular';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/css/bootstrap-theme.min.css';
import './style/example.css';

import appController from './app.controller';
import antPathExample from './ant-path-example.directive';

"use strict";
const appModule = angular.module('ant-path-example', []);

antPathExample(appModule.directive);
appController(appModule.controller);

appModule.run(['$templateCache'], $templateCache => {
    const templates = require.context('./templates', true, /\.html/);
    templates.keys().forEach(url => $templateCache.put(url, require(url)));
});


